
a { text-decoration:none;} 
a,
button,
input {
    -webkit-tap-highlight-color: rgba(255, 0, 0, 0);
    outline: none;
}

@import 'variable';
@import 'mixin';
@import 'reset';
@import 'mylei';

@import 'project';
@import 'order';
@import 'cart';

@import 'components.scss';
@import 'frame.scss';
@import 'header.scss';
@import 'foot_menu.scss';
@import 'push_info.scss';
@import 'search.scss';
@import 'goods/classify.scss';
@import 'goods/list.scss';
@import 'distribution/index.scss';
@import 'column.scss';
@import 'distribution/brokerage.scss';
@import 'distribution/detail.scss';
@import 'distribution/subordinate.scss';
@import 'distribution/extract.scss';
@import 'distribution/profit.scss';
@import 'distribution/qrcode.scss';
@import 'distribution/setting.scss';
@import 'distribution/ranking.scss';
@import 'distribution/boutique.scss';
@import 'distribution/shareholder.scss';
@import 'detail.scss';
@import 'motaichuang.scss';

html {
    max-width: 750px;
    margin: 0 auto;
}

html,
body {
    @include square($ful-width);
    overflow: auto;
}

.webkit_box {
    display: -webkit-box;
    /* autoprefixer: off */
    -webkit-box-orient: vertical;
    /* autoprefixer: on */
    height: $ful-width;
}

.webkit_content {
    flex: 1;
    -webkit-box-flex: 1;
    overflow: auto;
    position: relative;
    &.hide {
        overflow: hidden;
    }
}

.flex_1 {
    flex: 1;
    overflow: hidden;
}

input[type=checkbox],
input[type=radio] {
    margin: 0;
}

input,
textarea {
    @include placehold($nine-color);
}

input[disabled] {
    background-color:transparent;
}

figure {
    background: url('https://weixin.1919.cn/b2c1919/static/images/public/default.png') no-repeat center;
    background-size: contain;
}

.color_bg_deputy {
    background-color: $deputy-color;
}

.color_deputy {
    color: $deputy-color;
}

.color_bg_main {
    background-color: $main-color;
}

.color_main {
    color: $main-color;
}

.color_bg_white {
    background-color: $white-color;
}

.color_white {
    color: $white-color;
}

.color_nine {
    color: $nine-color;
}

.color_six {
    color: $six-color;
}

.padding_percent_3 {
    padding: 0 $percent_tre_width;
}

.border_btm_common {
    border-bottom: $common-border;
}

.text_indent {
    text-indent: 2em;
}

/*自己定义的模态框*/

.model_bg {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background: rgba(0, 0, 0, .6);
    z-index: 999;
    display: none;
}

.my_model {
    position: fixed;
    display: none;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    z-index: 9999;
    width: 300px;
    border: 1px solid #aeaeae;
    border-radius: 4px;
    padding: 10px;
    background: #fff;
}

.my_model .title {
    color: #333;
}

.my_model .title .closeModel {
    float: right;
    cursor: pointer;
}

.my_model .opBtn {
    display: flex;
    justify-content: space-around;
    margin-top: 10px;
}

.my_model .opBtn a {
    font-size: 12px;
    width: 80px;
    height: 28px;
    line-height: 28px;
    text-align: center;
    border-radius: 2px;
    display: inline-block;
}

.my_model .dialog-sure {
    background: $main-color;
    color: #fff;
}

.my_model .dialog-close {
    background: lightblue;
    color: $white-color;
}

//个人中心公共标题样式
.member_title {
    @extend .text_ellipsis;
    @include lineht($fourty-five-width);
    font-size: 14px;
    @extend .padding_percent_3;
    @extend .border_btm_common;
    border-bottom: $common-border;
}

@keyframes noticeShan {
    0% {
        opacity: 1;
    }
    25% {
        opacity: .5;
    }
    50% {
        opacity: 0;
    }
    75% {
        opacity: .5;
    }
    100% {
        opacity: 1;
    }
}

//商城公告
.notice {
    &_box {
        align-items: center;
        height: 40px;
        background-color: lighten($main-color,45%);
        .iconfont {
            margin-right: 10px;
            font-size: 20px;
            animation: noticeShan 1s infinite;
        }
    }
    &_cont {
        height: 40px;
        line-height: 40px;
        .bd ul li {
            @extend .text_ellipsis;
        }
    }
}

//底部导航样式重置
.am-navbar-default .am-navbar-nav {
    padding-top: 6px;
    background-color: $white-color;
}

.am-navbar-default a {
    color: #bbb;
    &.active {
        color: $deputy-color;
    }
    .iconfont {
        font-size: 24px;
    }
}

//折叠面板样式重置
.am-accordion-gapped {
    margin: 0;
}

.am-accordion-gapped .am-accordion-item.am-active {
    margin-bottom: 10px;
    border-bottom: none;
    background-color: $white-color;
}

.am-accordion-gapped .am-accordion-item {
    margin: 0;
    border: none;
    background-color: $white-color;
}

.am-accordion-gapped .am-accordion-title {
    font-size: 14px;
    height: 45px;
    line-height: 45px;
    padding: 0;
    padding-left: 3%;
    margin-bottom: 10px;
}

.am-accordion-gapped .am-active .am-accordion-title {
    background-color: $white-color;
}

.am-accordion-content {
    padding: 15px;
    font-size: 12px;
}

//轮播图样式重置
.am-direction-nav {
    display: none;
}

.am-slider-default .am-viewport {
    box-shadow: 0 2px 10px $ccc-color;
}

.am-slider-default .am-control-nav li a {
    @include size(12px,1px);
}

.am-slider-default .am-control-nav li a.am-active {
    background-color: $deputy-color;
}

//公共消息提醒样式
.common_tips {
    @include position($ful-width/2,
    $ful-width/2);
    line-height: 15px;
    margin-top: -$twenty-width;
    padding: 0 4px;
    border-radius: $twenty-width;
    @extend .font_size_11;
    @include colbg($white-color,
    $main-color);
}

//input[type='radio']的样式替换
input[type="radio"] {
    position: relative;
    border: none;
    background: transparent;
    @include square($twenty-width);
    -webkit-tap-highlight-color: transparent;
    appearance: none;
    -webkit-appearance: none;
}

input[type="radio"]::after {
    content: '';
    @include position(0,
    0);
    @include square($twenty-width);
    border: 1px solid $ccc-color;
    padding: 5px;
    border-radius: $ful-width/2;
    background-clip: content-box;
}

input[type="radio"]:checked::after {
    background-color: $deputy-color;
    border-color: $deputy-color;
}

input[type=checkbox], input[type=radio] {
    margin: 0;
}

//日历样式重置
.am-datepicker-success tr.am-datepicker-header {
    background-color: $deputy-color;
}

//选项卡公共样式
.tab {
    &_list {
        @include lineht(40px);
    }
    &_item {
        flex: 1;
        &.active {
            color: $deputy-color;
            border-bottom: 2px solid $deputy-color;
        }
    }
    &_cell {
        display: none;
        &.active {
            display: block;
        }
    }
}

//搜索框
.search {
    &_unit {
        width: 100%;
        @include lineht(30px);
        border-radius: 20px;
        background-color: #f5f4f2;
        &.index {
            @include lineht(36px);
            border-radius: 5px;
            background-color: $border-color;
        }
    }
    &_icon {
        margin-left: 12px;
        margin-right: 5px;
    }
    &_form {
        align-items: center;
        height: 50px;
    }
    &_back {
        @include square($fifty-width);
    }
    &_ipt {
        text-indent: 36px;
        background: $border-color url(../img/search_icon.png) no-repeat 12px;
        background-size: 15px 15px;
    }
    &_btn {
        width: 60px;
    }
    &_hot {
        &_title {
            @include lineht(40px);
        }
        &_list {
            padding-bottom: 10px;
        }
        &_item {
            width: 80px;
            margin-right: 8px;
            margin-bottom: 8px;
            float: left;
            @include lineht(24px);
            text-align: center;
            border-radius: 20px;
            color: $six-color;
            background-color: $border-color;
        }
    }
    &_box {
        position: relative;
        z-index: 3;
    }
}

.am-datepicker-success td span.am-active,
.am-datepicker-success td span.am-datepicker-old,
.am-datepicker-success td.am-datepicker-new,
.am-datepicker-success td.am-datepicker-old,
.am-datepicker-success td.am-active,
.am-datepicker-success td.am-active:hover,
.am-datepicker-success .am-datepicker-dow {
    color: $deputy-color;
}

.am-datepicker-success .am-datepicker-next-icon:hover,
.am-datepicker-success .am-datepicker-prev-icon:hover,
.am-datepicker-success .am-datepicker-select:hover {
    color: $white-color;
    background-color: transparent;
}

.am-field-valid,
.am-form-success .am-form-field {
    border-color: $deputy-color!important;
}

dt+dd {
    margin-top: 0;
}

.index {
    &_head {
        height: 50px;
    }
    &_nav {
        &_list {
            display: flex;
            flex-flow: wrap;
        }
        &_item {
            padding: 6px 0;
            width: $ful-width/5;
            span,
            a,
            img {
                display: block;   
            }
            img {
                overflow: hidden;
                margin: 4px auto;
                border-radius: 50%;
            }
            span {
                line-height: 25px;
            }
        }
    }
    &_brand {
        &_list {
            overflow-x: scroll;
            white-space: nowrap;
            padding: 12px 0;
        }
        &_item {
            display: inline-block;
            width: 72px;
            margin-left: 10px;
            &:nth-last-child(1){
                margin-right: 10px;
            }
        }
        &_thumb {
            overflow: hidden;
            margin: 0 auto;
            @include square(72px);
            border: $common-border;
        }
    }
    &_cheap {
        &_box {
            padding: 12px 3%;
        }
        &_thumb {
            overflow: hidden;
            width: 50%;
            max-height: 90px;
            float: left;
            &:nth-child(odd){
                padding-right: 3px;
            }
            &:nth-child(even){
                padding-left: 3px;
            }
        }
        &_pic {
            overflow: hidden;
            width: 24%;
            padding: 0 2px;
            height: 90px;
            float: left;
            margin-right: 1.3%;
            &:nth-last-child(1){
                margin-right: 0;
            }
        }
    }
}

.member {
    &_head {
        @include pad($twenty-width/2,
        $thirty-width);
    }
    &_top {
        @include lineht($thirty-five-width);
        padding-right: $twenty-width/2;
        a {
            position: relative;
            margin-left: $twenty-width;
            display: inline-block;
        }
    }
    &_thumb {
        overflow: hidden;
        @include square(46px);
        border-radius: $ful-width/2;
        img {
            @include square($ful-width);
        }
        &_lb {
            overflow: hidden;
            margin: $twenty-width auto;
            @include size($fifty-width*2,
            $fifty-width*2);
            border-radius: $ful-width/2;
            img {
                height: $ful-width;
            }
        }
    }
    &_rank {
        @include btn($thirty-five-width*2,
        $eighteen-width,
        $nine-color,
        $eighteen-width);
        &_top {
            padding: $twenty-width;
        }
        &_icon {
            @include size(85px,
            $twenty-width);
            padding-left: 7%;
            @include bg('../img/memmber-bg.png',
            cover);
        }
        &_list {
            line-height: 35px;
            h6,
            dt {
                font-weight: bold;
            }
            dt,
            dd,
            li,
            h6 {
                border-bottom: $common-border;
            }
            dd,
            li {
                border-right: $common-border;
            }
            p {
                padding: 10px;
                line-height: $twenty-width;
                color: $six-color;
            }
        }
        &_title {
            line-height: $twenty-width*2;
            font-weight: bold;
            .icon1 {
                color: limegreen;
            }
            .icon2 {
                color: $deputy-color;
            }
        }
        &_txt {
            text-align: center;
            background-color: $white-color;
            font-size: 13px;
            dd {
                float: left;
                width: $ful-width/3;
                color: $six-color;
            }
        }
        &_type {
            &.active {
                line-height: $fifty-width;
            }
            li {
                flex: 1;
                p {
                    text-align: left;
                }
            }
        }
        &_admit {
            @extend .border_btm_common;
        }
    }
    &_flex {
        h6 {
            width: 30%;
            vertical-align: middle;
            text-align: center;
            border-bottom: none;
        }
        p {
            flex: 1;
            border-left: $common-border;
        }
    }
    &_item {
        position: relative;
        float: left;
        width: $ful-width/5;
        text-align: center;
        @include pad($eighteen-width,
        $thirty-width/2);
        .iconfont {
            font-size: 24px;
            color: $deputy-color;
        }
    }
    &_tips {
        @extend .common_tips;
        margin-top: -$thirty-width;
    }
    &_status {
        @extend .font_size_13;
        position: relative;
        &:after {
            content: '';
            display: block;
            position: absolute;
            left: $ful-width/2;
            bottom: -4px;
            transform: translateX(-$ful-width/2);
            @include size($thirty-width,
            1px);
            background-color: $main-color;
        }
    }
    &_cell {
        @extend .member_item;
        padding: $twenty-width/2 0;
        width: $ful-width/4;
        .iconfont {
            font-size: $thirty-width;
            color: $deputy-color;
        }
        a {
            @include bigbtn(40px,18px,$main-color,$white-color,9px);
            margin: 0 auto;
        }
    }
    &_recommond {
        position: relative;
        @include lineht($fifty-width);
        &:before,
        &:after {
            content: '';
            display: block;
            position: absolute;
            top: $ful-width/2;
            @include size(30%,
            1px);
            background-color: $ccc-color;
        }
        &:before {
            left: $thirty-width;
        }
        &:after {
            right: $thirty-width;
        }
    }
    &_btn {
        margin: $twenty-width auto;
        @include bigbtn(88%,
        44px,
        $deputy-color,
        $white-color,
        4px);
        &[disabled] {
            background-color: $ccc-color;
        }
    }
    &_ipt {
        padding-left: 12px;
        margin: $twenty-width auto 0;
        @include bigbtn(88%,
        44px,
        $white-color,
        $tre-color,
        4px);
        border: 1px solid rgba(0, 0, 0, .2);
    }
    &_male {
        &_lb {
            @include size($ful-width,
            44px);
            line-height: 44px;
            @extend .border_btm_common;
        }
    }
    &_set {
        &_item {
            img {
                @include square($fifty-width);
            }
        }
        &_nickname {
            min-width: 50px;
        }
    }
    &_password {
        position: relative;
        overflow: hidden;
        &_toggle {
            position: absolute;
            right: 10%;
            top: 30px;
            color: $nine-color;
        }
    }
    &_msg {
        position: absolute;
        right: 10%;
        top:32px;
        line-height: 20px;
        background-color: transparent;
    }
    &_card {
        position: relative;
        @include lineht(45px);
        span {
            width: 25%;
        }
        &_ipt {
            flex: 1;
        }
    }
    &_invoice {
        &_item {
            padding-bottom: 5px;
            h6,
            p {
                @extend .flex_between;
                @extend .padding_percent_3;
            }
            h6 {
                margin-bottom: 5px;
                height: 35px;
                font-size: 14px;
                border-bottom: $common-border;
            }
            p {
                height: 25px;
            }
        }
        &_change {
            span {
                margin-right: 5px;
                width: auto;
            }
        }
        &_choose {
            align-items: center;
            height: 45px;
            label {
                width: $ful-width/3;
                display: flex;
                align-items: center;
            }
            input {
                margin-right: 5px;
            }
        }
    }
}

.common {
    &_goods_list {
        padding-left: 2%;
    }
    &_goods_item {
        position: relative;
        float: left;
        width: 48%;
        margin-right: 2%;
        margin-bottom: 2%;
        background-color: $white-color;
    }
    &_head {
        position: relative;
        @include size($ful-width,
        $fifty-width);
        line-height: $fifty-width;
        font-size: 17px;
        @extend .border_btm_common;
        @include colbg($white-color,
        $deputy-color);
        a {
            @include size($fifty-width, $fifty-width);
            line-height: $fifty-width;
        }
        .common_back {
            @include position(0,
            0);
            display: block;
        }
        .common_txt {
            position: absolute;
            right: 15px;
            top: 0;
        }
        &.white {
            @include colbg($tre-color,
            $white-color);
            .iconfont {
                color: $nine-color;
            }
        }
    }
    &_dele_btn {
        position: absolute;
        right: 0;
        top: 0;
    }
    &_nav {
        display: inline-block;
        position: relative;
        width: 100px;
        &:after {
            content: '';
            position: absolute;
            bottom: 0;
            left: 0;
            display: none;
            @include size(100px,1px);
            background-color: $deputy-color;
        }
        &.active {
            color: $deputy-color;
            &:after {
                display: block;
            }
        }
    }
}

.goods {
    &_title {
        @include rowhide(2,
        $twenty-width);
        span {
            padding: 0 2px;
            @include colbg($white-color,
            $main-color);
            border-radius: 2px;
        }
    }
    &_pic {
        position: relative;
    }
    &_gold {
        position: absolute;
        bottom: 0;
        left: 0;
        @extend .text_ellipsis;
        width: 100%;
        text-align: center;
        @include lineht(20px);
        color: $white-color;
        background: linear-gradient(to right,#f0b638, #f09b38);
    }
    &_wrap {
        padding: 10px;
    }
    &_status {
        @include position(10px,
        5px);
        @include size($twenty-width*2,
        26px);
        @extend .bg_contain;
    }
    &_price {
        span {
            display: inline-block;
            padding: 0 10px;
            @include colbg($white-color,$deputy-color);
            border-radius: 4px;
        }
    }
}

.node {
    &_list {
        overflow: hidden;
    }
    &_item {
        display: block;
        margin: 0 10px 10px;
        padding: 10px;
        background-color: $white-color;
    }
    &_cont {
        margin: 5px 0;
        padding-bottom: 10px;
        @extend .border_btm_common;
        text-align: justify;
    }
}

.information {
    &_item {
        padding: 11px;
        @extend .border_btm_common;
        img {
            display: block;
            @include size($fifty-width,
            $fifty-width);
            border-radius: 50%;
            overflow: hidden;
        }
    }
}

.about_us {
    &_ewm {
        margin: 40px auto;
        img {
            @include square(120px);
        }
    }
    p {
        line-height: 24px;
        text-align: justify;
    }
    &_phone {
        margin-top: 80px;
    }
}

.new {
    &_add_btn {
        @include bigbtn($ful-width, $fifty-width, $deputy-color, $white-color, 0);
    }
}

.invoice {
    &_box_none,
    &_goods_none,
    &_shop_none {
        display: none;
        @include position(0,0);
        padding-top: 200px;
        @include square($ful-width); 
    }
    &_box {
        &_icon {
            margin: 0 auto;
            @include square(70px);
            @include bg('../img/invoice_none_icon.png',contain);
        }
        &_bg {
            margin: 10px auto 0;
            @include size(200px,40px);
            @include bg('../img/common_bg_shadow.png',contain);
        }
    }
}

.adress {
    &_box_icon {
        background-image: url(../img/adress_none_icon.png);
    }
}

.footprint {
    &_box_icon {
        background-image: url(../img/footprint_none_icon.png);
    }
}

.collect {
    &_box_icon {
        background-image: url(../img/collect_none_icon.png);
    }
    &_cell {
        position: relative;
        display: none;
        &.active {
            display: block;
        }
    }
    &_goods_item,
    &_shop_item {
        position: relative;
        align-items: center;
        padding: 10px 15px;
        background-color: $white-color;
    }
    &_goods {
        &_title {
            @include lineht(45px);
        }
        &_thumb {
            overflow: hidden;
            margin-right: 10px;
            @include square(80px);
            border: $common-border;
        }
        &_type {
            overflow: hidden;
            flex: 1;
        }
        &_dele {
            position: absolute;
            bottom: 10px;
            right: 15px;
            text-decoration: underline;
        }
    }
    &_shop_dele {
        @extend .collect_goods_dele;
    }
}

.color_lightgreen {
    color: #01e401;
}

.color_lightcoral {
    color: lightcoral;
}

.color_lightskyblue {
    color: lightskyblue;
}

.color_lightsalmon {
    color: lightsalmon;
}

.kefu {
    &_btn {
        height: 50px;
        a {
            display: block;
            margin: 10px auto;
            @include bigbtn(120px,30px,$deputy-color,$white-color,6px);
        }
    }
    &_item {
        width: $ful-width/4;
        float: left;
        padding: 20px 0 15px;
    }
    &_lb {
        padding-top: 14px;
        @include size(22%,64px);
        border-right: $common-border;
    }
    &_item_cell {
        float: left;
        width: $ful-width/2;
        @include lineht(32px);
        border-bottom: $common-border;
        &:nth-child(odd) {
            border-right: 2px solid $border-color;
        }
    }
}

.order {
    &_menu {
        @include lineht(50px);
        overflow-x: scroll;
        white-space: nowrap;
    }
    &_item {
        position: relative;
        display: inline-block;
        font-size: 14px;
        padding: 0 20px;
        &:after {
            content: '';
            position: absolute;
            bottom: 1px;
            left: 50%;
            display: none;
            @include size(80px,1px);
            transform: translateX(-50%);
            background-color: $deputy-color;
        }
        &.active {
            color: $deputy-color;
            &:after {
                display: block;
            }
        }
    }
    &_goods {
        &_total {
            @include lineht(40px);
        }
        &_status {
            a {
                display: inline-block;
                padding: 0 12px;
                margin-right: 12px;
                border-radius: 20px;
                border: 1px solid $ccc-color;
                &:nth-last-child(1) {
                    margin-right: 0;
                }
            }
        }
    }
}

.peisong {
    &_arrow {
        @include square(30px);
    }
}

.mendian {
    &_top {
        margin-top: 15px;
        @include lineht($thirty-width);
    }
    &_ipt {
        flex: 1;
        text-indent: 10px;
        border-right: 1px solid $ccc-color;
    }
    &_btn {
        width: 60px;
        color: $six-color;
    }
}

.coupon {
    &_item {
        position: relative;
        height: 90px;
        background: url(../img/coupon_bg.png) 0 -15px repeat-x;
        background-size: 100% auto;
    }
    &_time {
        position: absolute;
        bottom: 0;
        width: calc(100% - 90px);
        border-top: $common-border;
    }
    &_box {
        color: $quan-blue-color;
    }
    &_tips {
        @include position(10px,0);
        max-width: 160px;
        @include lineht(18px);
        border-bottom-left-radius: 7px;
        border-bottom-right-radius: 7px;
        @include colbg($white-color,$quan-yellow-color);
    }
    &_price {
        padding-top: 10px;
    }
    &_status {
        position: relative;
        width: 70px;
        height: $ful-width;
        background: url(../img/coupon_had.png) no-repeat center 10px;
        background-size: 50px 50px;
        background-color: $quan-blue-color;
        button {
            position: absolute;
            left: 8px;
            bottom: 10px;
            @include bigbtn(56px,16px,#fce119,$quan-blue-color,6px);
        }
        &:before,
        &:after {
            content: '';
            position: absolute;
            @include square(12px);
            background-color: $border-color;
            border-radius: $ful-width;
            left: -6px;
        }
        &:before {
            top: -6px;
        }
        &:after {
            bottom: -6px;
        }
    }
    &_gq {
        .coupon_status {
            background-image: url(../img/coupon_guoqi.png);
            background-position: center 20px;
            background-color: $ccc-color;
        }
        .coupon_tips {
            background-color: $ccc-color;
        }
        .coupon_box {
            color: $ccc-color;
        }
    }
    &_use {
        .coupon_status {
            background-image: url(../img/coupon_use.png);
        }
    }
    &_go {
        .coupon_status {
            background-image: url(../img/coupon_go.png);
            background-color: $white-color;
            button {
                @include colbg($white-color,$quan-yellow-color);
            }
        }
    }
    &_more {
        font-size: 36px;
        line-height: 1;
        padding: 2px 0;
        color: $border-color;
        text-transform: uppercase;
    }
    &_yellow_color {
        color: $quan-yellow-color;
    }
    &_get {
        .coupon_status {
            background-image: none;
        }
        .coupon_percent {
            @include position(50%,10px);
            transform: translateX(-50%);
            @include square(50px);
            padding-top: 5px;
            text-align: center;
            color: $white-color;
            border: 2px solid $white-color;
            border-radius: 50%;
        }
    }
    &_finish {
        .coupon_status {
            background-color: $ccc-color;
            button {
                @include colbg($white-color,#9c9c9c);
            }
        }
        .coupon_percent {
            padding-top: 0;
            line-height: 50px;
        }
    }
    &_detail {
        &_top {
            padding-top: 15px;
            border-top-left-radius: 3px;
            border-top-right-radius: 3px;
            background-color: $quan-blue-color;
            a {
                padding: 0 4px;
                border: 1px solid $white-color;
                border-radius: 3px;
            }
            span {
                font-weight: bold;
            }
        }
        &_second {
            margin-bottom: 20px;
        }
        &_mode {
            border-bottom-left-radius: 3px;
            border-bottom-right-radius: 3px;
        }
    }
}

//支付成功
.sucess {
    &_icon {
        margin: $twenty-width 0;
    }
    &_btn {
        @include inline-btn(120px, 30px, $deputy-color,6px);
        margin: 12px auto 0;
    }
}

.rank {
    &_top {
        position: relative;
        height: 180px;
        background-position: bottom center;
        background-repeat: no-repeat;
        background-size: cover;
    }
    &_dope {
        background-image: url(../img/rank_bg.jpg);
    }
    &_custom {
        background-image: url(../img/rank_bg_blue.jpg);
    }
    &_withdraw {
        background-image: url(../img/rank_bg_yellow.jpg);
    }
    &_posi {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
    }
    &_place {
        @include size(120px,115px);
        padding-top: 30px;
        @include bg('../img/zhang_bg.png',contain);
        p {
            margin-top: 20px;
        }
    }
    &_week {
        left: 10%;
    }
    &_total {
        right: 10%;
    }
    &_sum {
        color: $gold-color;
    }
    &_table {
        background-color: #ffcc03;
        &.blue {
            background-color: #138ed2;
        }
        &.yellow {
            background-color: #d25713;
        }
        table {
            margin-bottom: 0;
            thead th {
                font-weight: normal;
                text-align: center;
            }
        }
        td {
            vertical-align: middle!important;
        }
        td:nth-child(1){
            text-align: center;
            font-weight: bold;
        }
        td:nth-child(2){
            display: flex;
            align-items: center;
            img {
                border-radius: 50%;
            }
            span {
                margin-left: 10px;
            }
        }
        td:nth-child(3){
            text-align: center;
            color: $main-color;
            font-size: 16px;
        }
    }
}
