//红色
$color-size-red : #ff2c29;
//分割线
$color-line-gray : #eee;
//灰色背景
$color-background-gray : #f7f7f7;
//橙色背景
$color-background-orange : #ffa800;
//橙红背景
$color-background-orangered : #ff6333;
//字体颜色分级
$color-size1 : #000;
$color-size2 : #444;
$color-size3 : #222;
$color-size4 : #505050;
$color-size5 : #666;
$color-size6 : #999;
//灰色字体
$color-size-gray : #7f7f7f;

// 宽度和高度的设置
@mixin box-size($w,$h) {
    width: $w;
    height: $h;
}
//文本设置
@mixin box-txt($h,$s) {
    height: $h;
    line-height: $h;
    font-size: $s;
}

@mixin box-borber($bw,$r,$c,$b) {
    border: $bw solid $c;
    border-radius:  $r;
    background: $b;
}

//文本框的定位与显示
@mixin box-txts ($tb,$lr,$h,$s,$c){
    padding: $tb $lr;
    text-align: center;
    line-height: $h;
    .txt {
        line-height: $s;
        font-size: $s;
        color: $c;
    }
}

@mixin box-txtd ($h,$s,$c){
    line-height: $h;
    .txt {
        font-size: $s;
        color: $c;
    }
}

@mixin border ($f){
    border#{$f}: 1px solid $color-line-gray;
}

//文本段落限制
@mixin rowhide($num, $lht) {
    display: -webkit-box;
    overflow: hidden;
    text-overflow: ellipsis;
    /* autoprefixer: off */
    -webkit-box-orient: vertical;
    /* autoprefixer: on */
    -webkit-line-clamp: $num;
    line-height: $lht;
    height: $lht*$num;
}

@mixin  background-url($u) {
    background: url($u) no-repeat;
    background-size: 100% 100%;
}

%row_parent_flex {
    display: flex;
    align-items:center;
}

%column_parent_flex {
    display: flex;
    flex-direction:column;
}