.distribution {
    &_setting {
        &_box {
            padding-top: 10px;
        }
        &_info {
            background: #fff;
            padding: 0 10px;
        }
        &_wrap {
            display: flex;
            align-items: flex-start;
            padding: 10px;
            border-bottom: 1px solid $color-line-gray;
        }
        &_wrap:nth-child(2) {
            height: 90px;
        }
        &_wrap:nth-child(3) {
            height: 120px;
        }
        &_left {
            width: 60px;
            font-size: 16px;
            color: $color-size4;
        }
        &_right {
            flex: 1;
            display: flex;
            align-items: center;
            height: 100%;
            &_input {
                border: 0;
                outline: none;
                @include box-size(100%,100%);
                font-size: 14px;
                &::placeholder {
                    color: $color-size6;
                }
            }
            &_textarea {
                border: 0;
                outline: none;
                @include box-size(100%,80px);
                font-size: 14px;
                &::placeholder {
                    color: $color-size6;
                }
            }
        }
        &_icon {
            font-size: 40px;
            &_input[type=file] {
                display: none;
            }
        }
        &_logo {
            font-size: 70px;
            color: $color-size6;
            &_input[type=file] {
                display: none;
            }
        }
        &_btn {
            padding: 20px 15px;
            &_wrap {
                width: 100%;
                height: 40px;
                border: 0;
                border-radius: 5px;
                font-size: 16px;
                background: #fea23d;
                color: #fff;
            }
        }
    }
}