.header {
    &_box {
        display: flex;
        align-items: center;
        padding: 0 10px;
        @include border("-bottom");
        height: 50px;
        background: #f7f7f7;
    }
    &_left {
        width: 60px;
        .iconfont {
            font-size: 25px;
        }
    }
    &_center {
        flex: 1;
        text-align: center;
    }
    &_right {
        width: 60px;
        height: 20px;
        &_txt {
            font-size: 14px;
            color: $color-size6;
        }
    }
}