.search {
    &_box {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 10px;
        background: #fff;
    }
    &_wrap {
        flex: 1;
        display: flex;
        justify-content: space-between;
        align-items: center;
        border-radius: 5px;
        overflow: hidden;
        background: #f4f4f4;
        &_icon {
            float: left;
            margin:0 10px;
            @include box-size(25px,25px);
            .icon-ziyuan {
                font-size: 20px;
                color: #d4cfcf;
            }
        }
        &_input {
            flex: 1;
			-webkit-box-flex: 1;
			overflow: auto;
            border: 0;
            height: 35px;
            font-size: 15px;
            text-align: left;
            background: transparent;
            outline: none;
        }
    }
    &_switcher {
        float: left;
        margin:0 5px;
        @include box-size(20px,20px);
        .icon-suoyoushangpin {
            font-size: 20px;
        }
    }
}