.distribution {
    &_index {
        &_box {
            background: $color-background-gray;
            overflow: hidden;
        }
        &_wrap {
            
        }
        &_header {
            &_box {
                @extend %row_parent_flex;
                justify-content:space-between;
                height: 120px;
                background: #fea23d;
            }
            &_wrap {
                @extend %row_parent_flex;
            }
            &_icon {
                margin-left: 10px;
                border-radius: 40px;
                @include box-size(80px,80px);
                @include background-url('../../img/header.jpg');
            }
            &_title {
                margin-left: 10px;
            }
            &_name {
                @include box-txts(0,0,25px,22px,#fff);
                text-align: left;
            }
            &_recommend {
                @include box-txts(0,0,21px,14px,#fff);
                font-weight: 100;
            }
            &_class {
                @include box-txts(0,0,21px,14px,#fff);
                font-weight: 100;
            }
            &_setting {
                margin-right: 10px;
                @include box-size(20px,80px);
                .iconfont {
                    color: #fff;
                }
            }
        }
        &_invite {
            &_box {
                margin-top: 10px;
                height: 60px;
                @extend %row_parent_flex;
                background: #fff;
            }
            &_icon {
                margin-left: 10px;
                @include box-size(18px,18px);
            }
            &_txt {
                margin-left: 10px;
                @include box-txts(0,0,15px,15px,$color-size3);
            }
        }
        &_brokerage {
            &_box {
                margin-top: 10px;
                padding-left: 10px;
                height: 180px;
                @extend %column_parent_flex;
                background: #fff;
            }
            &_wrap {
                flex: 1;
                position: relative;
                @extend %column_parent_flex;
                align-items: flex-start;
                padding:20px 0;
                border-bottom: 1px solid $color-line-gray;
                width: 100%;
            }
            &_price {
                font-weight: bold;
                @include box-txtd(18px,18px,#ff8000);
            }
            &_info {
                @include box-txtd(30px,15px,$color-size4);
            }
            &_btn {
                position: absolute;
                top:30px;
                right: 10px;
                border: 0;
                border-radius: 15px;
                @include box-txts(8px,15px,15px,15px,#fff);
                background-color: $deputy-color;
            }
            &_btn.disabled {
                background-color: $ccc-color;
            }
        }
        &_card {
            &_box {
                margin-top: 10px;
                height: 240px;
                background: #fff;
                @extend %column_parent_flex;
            }
            &_wrap {
                flex: 1;
                @extend %row_parent_flex;
                &+.distribution_index_card_wrap {
                    border-top: 1px solid $color-line-gray;
                }
            }
            &_item {
                flex: 1;
                height: 100%;
                @extend %row_parent_flex;
                border-right: 1px solid $color-line-gray;
            }
            &_icon {
                display: flex;
                align-items: center;
                justify-content: center;
                margin-left: 10px;
                @include box-size(50px,50px);
                .iconfont {
                    font-size: 35px;
                }
                .icon_brokerage{
                    color: #ff4357;
                }
                .icon_detail {
                    color: #9ec9f4;
                }
                .icon_extract{
                    color: #ffbe2e;
                }
                .icon_ranking {
                    color: #ff6e02;
                }
                .icon_profit {
                    color: #ff6e02;
                }
            }
            &_info {
                @extend %column_parent_flex;
                align-content:center;
                margin-left: 10px;
                height: 50px;
            }
            &_name {
                flex: 1;
                display: flex;
                @include box-txts(0,0,16px,16px,$color-size3);
                align-items:center;
                text-align: left;
            }
            &_num {
                flex: 1;
                @include box-txts(0,0,20px,14px,$color-size6);
                text-align: left;
            }
        }
        &_generalize {
            margin-top: 10px;
            .column_wrap {
                border: 0;
            }
            .iconfont {
                color: #ffb34f;
            }
        }
        &_setting {
            margin-top: 10px;
            .iconfont {
                color: #ffb34f;
            }
        }
        &_ranking {
            .iconfont {
                color: #ffb34f;
            }
        }
        &_shareholder {
            .iconfont {
                color: #ffb34f;
            }
        }
        &_agency {
            .iconfont {
                color: #ffb34f;
            }
        }
    }
}