.distribution {
    &_profit {
        &_box {

        }
        &_header {@extend %column_parent_flex;
            height: 150px;
            @include background-url('../../img/myred.jpg');
        }
        &_info {
            padding-top: 15px;
            &_wrap {
                background: #fff;
            }
        }
        &_title {
            .txt {
                font-size: 16px;
                color: #1b1a20;
                font-weight: 300;
            }
        }
        &_msg {
            padding: 10px;
            @include box-txtd(20px,12px,$color-size2);
            .msgtxt {
                font-size: 14px;
                font-weight: 800;
                color: #cfab6c;
            }
        }
        &_btn {
            padding: 15px;
            &_wrap {
                display: flex;
                align-items: center;
                padding: 10px;
                border: 0;
                border-radius: 5px;
                width: 100%;
                @include box-txtd(20px,16px,#cfab6c);
                letter-spacing: 2px;
                background: #1b1a20;
                .txt {
                    flex: 1;
                }
            }
        }
    }
}