.distribution {
    &_brokerage {
        &_box {
            background: $color-background-gray;
            height: 100%;
        }
        &_wrap {
            position: relative;
            height: 100%;
        }
        &_header {
            @extend %column_parent_flex;
            align-items:center;
            height: 150px;
            background: #fea23d;
            &_price {
                flex: 1;
                display: flex;
                align-items: flex-end;
                @include box-txtd(32px,32px,#fff);
            }
            &_title {
                flex: 1;
                @include box-txts(10px,0,24px,15px,#fff);
            }
        }
        &_may {
            margin-top: 10px;
            .maytxt {
                color: #ff8000;
            }
        }
        &_apply {
            margin-top: 10px;
        }
        &_waitreceiv {
            margin-top: 10px;
        }
        &_info {
            margin-top: 10px;
            background: #fff;
        }
        &_msg {
            padding: 10px;
            height: 70px;
            font-size: 14px;
            color: $color-size6;
            .msgtxt {
                color: #ff8000;
            }
        }
        &_footer {
            width: 100%;
        }
        &_btn {
            display: flex;
            border: 0;
            height: 40px;
            width: 100%;
            background: #ccc;
            .txt {
                flex: 1;
                font-size: 14px;
                font-weight: lighter;
                letter-spacing: 2px;
                color: #fff;
            }
        }
        &_btn.active {
            background-color: $deputy-color;
        }
    }
}