.project_banner {

	// .am-slides li {
	// 	padding-top: 100%;
	// }
	.am-control-nav {
		display: none;
	}

	.am-slider-default .am-viewport {
		box-shadow: none;
	}
}

.project-canshu,
.project-model,
.project-jifen {
	padding: 0 12px;
	border-bottom: 10px solid #f5f5f5;
}

.project-title {
	-webkit-box-flex: 1;
	-ms-flex: 1;
	flex: 1;
	display: -webkit-box;
	overflow: hidden;
	text-overflow: ellipsis;
	/* autoprefixer: off */
	-webkit-box-orient: vertical;
	/* autoprefixer: on */
	-webkit-line-clamp: 2;
	color: #333;
	font-size: 14px;
	line-height: 20px;
}

.project-intrest {
	width: 30px;
	padding-top: 30px;
	background-image: url(../img/intrest-bg.png);
	background-repeat: no-repeat;
	background-position: 0 0;
}

.project-intrest.active {
	color: #e23b33;
	background-position: -30px 0;
}

.project-price {
	margin-top: 8px;
	margin-bottom: 4px;
	font-weight: bold;
	color: #e23b33;
	font-size: 16px;
	align-items: flex-end;
}

.project-yongjin {
	padding: 0 10px;
	font-size: 14px;
	background-color: $main-color;
	color: #fff;
	border-radius: 6px;
}

.project-kill {
	margin-top: 0;
}

.project-kill:before {
	content: "秒杀中：￥";
	color: #e23b33;
	display: inline-block;
}

.project-vip {
	margin-top: 0;
}

.project-vip:before {
	content: "会员价：￥";
	color: #e23b33;
	display: inline-block;
}

.project-time {
	font-weight: bold;
}

.project-time:before {
	content: "还剩";
	color: #e23b33;
	display: inline-block;
}

.project-number {
	color: #e23b33;
}

.project-orig {
	text-decoration: line-through;
	color: #999;
}

.project-orig:before {
	content: "￥";
	color: #999;
	display: inline-block;
}

.project-orig:before {
	text-decoration: line-through;
}

.project-minute:before,
.project-second:before {
	content: ":";
	color: #e23b33;
	display: inline-block;
}

.project-model,
.project-jifen {
	height: 50px;
	color: #666;
}

.project-model img,
.project-jifen img {
	vertical-align: middle;
}

.project-mod {
	width: 60px;
	margin-right: 8px;
	line-height: 24px;
	border-radius: 4px;
	border: 1px solid #eaeaea;
}

.project-mod:hover,
.project-mod.active {
	border-color: #e23b33;
	color: #fff;
	background-color: #e23b33;
}

.project-value {
	padding-top: 4px;
	text-decoration: underline;
}

.project-value:before {
	content: "领券：";
	color: #999;
	display: inline-block;
}

.project-value:before {
	text-decoration: none;
}

.project-node {
	padding: 4px 0;
	border-bottom: 1px solid #eaeaea;
	color: #e23b33;
}

.project-node:before {
	content: "购买此商品可获得";
	color: #333;
	display: inline-block;
}

.project-node:after {
	content: "积分";
	display: inline-block;
	color: #333;
}

.project-info {
	color: #bbb;
	line-height: 30px;
}

.project-one:before {
	content: "销量：";
	color: #bbb;
	display: inline-block;
}

.project-two:before {
	content: "当前库存：";
	color: #bbb;
	display: inline-block;
}

.project-log {
	line-height: 20px;
	color: #333;
}

.project-log:before {
	content: "配送：";
	color: #bbb;
	display: inline-block;
}

.project-num {
	height: 42px;
}

.project-num:before {
	content: "数量：";
	color: #bbb;
	display: inline-block;
}

.project-shop {
	padding: 10px;
	background-color: #fff;
	border-bottom: 10px solid #f5f5f5;
}

.project-logo {
	height: 60px;
	overflow: hidden;
}

.project-img {
	display: block;
	margin-right: 8px;
}

.project-dianming,
.project-intro {
	line-height: 1.2;
}

.project-intro {
	color: #999;
}

.project-biankuang {
	width: 80px;
	height: 30px;
	line-height: 28px;
	color: $deputy-color;
	border: 1px solid $deputy-color;
	border-radius: 10px;
	text-align: center;
}

.project-wenzi {
	margin-left: 4px;
}

.project-page {
	margin-right: 8px;
	color: #bbb;
}

.project-form {
	padding: 8px;
	width: 70px;
	height: 22px;
	line-height: 22px;
	background-color: transparent;
	color: #333;
	border: 1px solid #eaeaea;
	border-radius: 4px;
	text-align: center;
	font-size: 12px;
}

.project-minus,
.project-plus {
	width: 10px;
}

.project-ipt {
	width: 100%;
	text-align: center;
}

.project-details {
	width: 100%;
	text-align: center;
	padding: 10px;
	overflow: hidden;
	border-bottom: 10px solid #f5f5f5;
}

.project-text {
	color: #333;
}

.project-box {
	position: relative;
	padding: 10px;
	height: 90px;
	margin: 0;
	background: url(../img/shop-bg.png) no-repeat center;
	background-size: cover;
}

.project-attention {
	position: absolute;
	right: 20px;
}

.project-push {
	width: 80px;
	height: 22px;
	line-height: 20px;
	color: #e23b33;
	border: 1px solid #e23b33;
}

.project-del {
	margin-top: 6px;
	width: 80px;
	height: 22px;
	line-height: 20px;
	color: #f85;
	border: 1px solid #f85;
}

.function {
	background-color: #f4f3f3;
	width: 100%;
	height: 50px;
}

.function-box {
	-webkit-box-flex: 1;
	-ms-flex: 1;
	flex: 1;
}

.function-kefu,
.function-cart {
	width: 50px;
}

.function-kefu img,
.function-cart img {
	width: 22px;
	height: 22px;
}

.function-cart {
	position: relative;
}

.function-num {
	position: absolute;
	right: 0;
	top: 0;
	width: 14px;
	height: 14px;
	line-height: 14px;
	background-color: #e23b33;
	color: #fff;
	border: 1px solid none;
	border-radius: 50%;
	text-align: center;
}

.function-btn {
	width: 250px;
}

.function-join {
	width: 124px;
	height: 50px;
	line-height: 50px;
	background-color: #f85;
	color: #fff;
	border: 1px solid none;
	border-radius: 0;
	text-align: center;
}

.function-buy {
	width: 124px;
	height: 50px;
	line-height: 50px;
	background-color: #e23b33;
	color: #fff;
	border: 1px solid none;
	border-radius: 0;
	text-align: center;
}

.jifen {
	position: fixed;
	bottom: 0;
	left: 0;
	display: none;
	width: 100%;
	height: 200px;
	font-size: 12px;
	background-color: #fff;
	z-index: 99999;
}

.jifen-box {
	padding: 10px;
	height: 150px;
	line-height: 30px;
}

.jifen-button {
	width: 100%;
	height: 50px;
	line-height: 50px;
	text-align: center;
	color: #fff;
	background-color: #e23b33;
}

.jifen-list {
	-ms-flex-flow: wrap;
	flex-flow: wrap;
}

.jifen-list li {
	margin: 10px 0;
	width: 33.33%;
}

.jifen-icon {
	width: 60px;
	height: 60px;
	margin: 10px auto;
}

.jifen-buy {
	font-size: 12px;
	text-align: center;
}

.jifen-btn {
	margin: 10px auto;
	width: 80px;
	height: 24px;
	font-size: 12px;
	line-height: 24px;
	background-color: #f85;
	color: #fff;
	text-align: center;
	border-radius: 4px;
}

.jifen-pic {
	margin: 10px auto;
	text-align: center;
}

.common-bg {
	position: fixed;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	display: none;
	background-color: rgba(0, 0, 0, 0.6);
	z-index: 999;
}