.distribution {
    &_extract {
        &_box {
            @extend %column_parent_flex;
        }
        &_header {
            @extend %row_parent_flex;
            height: 60px;
            background: #fea23d;
            &_wrap {
                margin-left: 10px;
                @include box-txtd(16px,16px,#fff);
            }
        }
        &_option {
            &_box {
                @extend %row_parent_flex;
                background: #fff;
                border-bottom: 1px solid $color-line-gray;
                .active {
                    border-bottom: 1px solid #fea23d;
                    .txt {
                        color: #fea23d;
                    }
                }
            }
            &_wrap {
                flex: 1;
                @extend %row_parent_flex;
                height: 60px;
                border-bottom: 1px solid #fff;
                .txt {
                    flex: 1;
                    @include box-txtd(14px,14px,$color-size3);
                    text-align: center;
                }
            }
        }
        &_nothing {
            flex: 1;
            padding-top: 100px;
            &_wrap {
                @extend %column_parent_flex;
                align-items: center;
            }
            &_icon {
                @include box-size(150px,150px);
                .iconfont {
                    font-size: 140px;
                    color: $color-size6;
                }
            }
            &_txt {
                margin-top: 30px;
                @include box-txtd(16px,16px,$color-size6);
            }
        }
        &_anying {
            flex: 1;
        }
    }
}