.distribution {
    &_boutique {
        &_box {
            display: flex;
            flex-direction: column;
        }
        &_header {
            height: 180px;
            @include background-url('../../img/distribution_boutique.jpg');
        }
        &_info {
            display: flex;
            padding: 5px 0;
            height: 50px;
            background: #fff;
            &_wrap {
               flex: 1;
               display: flex;
               flex-direction: column;
               align-items: center;
               &+.distribution_boutique_info_wrap {
                   border-left: 1px solid $color-line-gray;
               }
            }
            &_logo {
                margin-top: -35px;
                @include box-size(75px,75px);
                @include border("");
                @include background-url('../../img/header.jpg');
            }
            &_num {
                flex: 1;
                display: flex;
                align-items: center;
            }
            &_numtxt {
                flex: 1;
                display: flex;
                align-items: center;
            }
            &_icon {
                flex: 1;
                display: flex;
                align-items: center;
            }
            &_txt {
                flex: 1;
                display: flex;
                align-items: center;
            }
        }
        &_serach {
            padding: 10px 20px;
            &_input {
                border: 0;
                border-radius: 5px;
                outline: none;
                width: 100%;
                height: 35px;
                font-size: 16px;
                text-align: center;
                background: #fff;
            }
        }
        &_recommend {
            margin-top: 25px;
            @include border("-top");
            &_title {
                display: flex;
                flex-direction: column;
                align-items: center;
            }
            &_txt {
                margin-top: -12px;
                padding: 0 10px;
                background: #f4f4f4;
                color: #ff4357;
                .iconfont {
                    font-size: 14px;
                }
            }
        }
        &_commodity {
            flex: 1;
        }
    } 
}