.foot {
    &_menu {
        &_box {
            display: flex;
            height: 50px;
            background: #fff;
            .active {
                .foot_menu_item {
                    color: #ff0000;
                }
            }
        }
        &_wrap {
            flex: 1;
            display: flex;
            flex-direction: column;
            padding-top: 4px;
            padding-bottom: 4px;
        }
        &_item {
            flex: 1;
            display: flex;
            flex-direction: column;
            color: $color-size6;
        }
        &_icon {
            flex-grow: 3;
            display: flex;
            flex-direction: column;
            align-items: center;
            .iconfont {
                font-size: 25px;
            }
        }
        &_txt {
            flex-grow: 1;
            display: flex;
            flex-direction: column;
            align-items: center;
            font-size: 14px;
        }
    }
}