
.order {
    &-num {
        font-size: 13px;
        @include colbg($white-color,
        $six-color);
    }
    &-name {
        @include before('订单编号：',
        $nine-color,
        inline-block);
    }
    &-time {
        color: $nine-color;
    }
    &-choice {
        position: relative;
        @include size(16px,
        96px);
    }
    &-box {
        position: absolute;
        top: 40px;
        left: 0;
        @include size(16px,
        16px);
    }
    &-form {
        @include size(16px,
        16px);
        @include img-bg('../img/icon-choice.png',
        100%,
        100%);
        &.mark {
            @include img-bg('../img/icon-choice-hov.png',
            100%,
            100%);
        }
    }
    &-pic {
        @include size(80px,
        80px);
        margin: 0 6px;
    }
    &-cont {
        flex: 1;
        font-size: 13px;
    }
    &-kind {
        height: 32px;
    }
    &-title {
        height: 40px;
        @include rowhide(2,20px);
    }
    &-mark {
        color: $deputy-color;
        font-weight: bold;
    }
    &-back {
        color: $nine-color;
        text-decoration: underline;
    }
    &-price,
    &-btn {
        background-color: $white-color;
    }
    &-price {
        font-size: 13px;
        color: $deputy-color;
        font-weight: bold;
        border-bottom: 1px solid $border-color;
        @include before('总计：',
        $tre-color,
        inline-block);
        &:before {
            font-weight: normal;
        }
    }
    &-btn {
        button {
            @include btn(68px,
            28px,
            $six-color,
            4px);
        }
    }
    &-none {
        padding-top: 50px;
    }
    &-img {
        @include size(180px,
        120px);
        margin: 0 auto;
    }
    &-state {
        margin: 8px 0;
        color: $nine-color;
    }
    &-button {
        @include btn(80px,
        24px,
        $main-color,
        4px);
        margin: 0 auto;
    }
    &-dianpu {
        margin-left: 8px;
        &:before {
            content: '店铺名称：';
            display: inline-block;
        }
    }
    &-shuliang {
        color: $nine-color;
        &:before {
            content: '数量：';
            display: inline-block;
        }
    }
    &-border {
        border-bottom: 1px solid $border-color;
    }
    &-cell {
        display: inline-block;
        margin-right: 6px;
        @include size(40px,18px);
        line-height: 18px;
        text-align: center;
        font-size: 8px;
        background-color: $bg-color;
        border-radius: 4px;
        color: $six-color;
    }
    &-xiaoji {
        height: 40px;
        line-height: 32px;
        padding-right: 12px;
        text-align: right;
        font-size: 12px;
        border-bottom: 8px solid $border-color;
        span {
            font-weight: bold;
            color: $main-color;
        }
    }
    &-jine {
        font-size: 16px;
    }
}


/*order*/