.push {
    &_info {
        &_box {
            background: #333;
        }
        &_wrap {
            overflow: hidden;
        }
        &_icon {
            float: left;
            margin: 10px 5px;
            @include box-size(40px,40px);
            background: #fff;
        }
        &_title {
            float: left;
            margin-top: 10px;
            margin-left: 15px;
        }
        &_slogan {
            @include box-txts(0,0,14px,14px,#fff);
            text-align: left;
        }
        &_btn {
            float: right;
            margin-right: 15px;
            margin-top: 15px;
            border: 0;
            border-radius: 4px;
            @include box-txts(10px,10px,10px,10px,#fff);
            background: #04be02;
        }
    }
}