.column {
    &_box {
        background: #fff;
        padding-left: 10px;
    }
    &_wrap {
        @extend %row_parent_flex;
        padding-right:15px; 
        justify-content: space-between;
        border-bottom: 1px solid $color-line-gray;
        height: 50px;
    }
    &_icon {
        @include box-size(16px,16px);
        display: flex;
        flex-direction: column;
        .iconfont {
            font-size: 16px;
        }
        &+.column_title {
            padding-left: 10px;
        }
    }
    &_title {
        flex: 1;
        @extend %row_parent_flex;
        justify-content: space-between;
        &+.column_arrow {
            margin-left: 10px;
        }
    }
    &_left {
        &_txt {
            @include box-txtd(14px,14px,$color-size2);
        }
    }
    &_center {
        &_txt {
            flex: 1;
            padding: 0 10px;
            @include box-txtd(14px,14px,$color-size2);
        }
    }
    &_right {
        &_txt {
            @include box-txtd(14px,14px,$color-size2);
            text-align: right;
        }
    }
    &_arrow {
        display: flex;
        flex-direction: column;
        transition: all 0.3s ease-in-out;
        @include box-size(14px,14px);
        .iconfont {
            font-size: 14px;
            color: #999;
        }
    }
    &_arrow.rotate {
        transform: rotate(-90deg);
    }
}