.distribution {
    &_shareholder {
        &_box {
            
        }
        &_header {
            height: 120px;
            @include background-url('../../img/red.png');
        }
        &_info {
            padding: 10px 10px 0 10px;
            background: #fff;
            font-size: 15px;
            &_title {
                display: flex;
                align-items: center;
                padding-left: 5px;
                height: 50px;
                font-size: 15px;
                .titletxt {
                    color: #ff4357;
                }
            }
        }
        &_wrap {
            display: flex;
            @include border("-bottom");
            height: 50px;
        }
        &_left {
            width: 60px;
            display: flex;
            align-items: center;
            font-size: 15px;
            color: $color-size4;
            letter-spacing: 2px;
        }
        &_right {
            flex: 1;
            display: flex;
            &_icon {
                width: 10px;
                display: flex;
                align-items: center;
                .iconfont {
                    size: 10px;
                    color: #ff4357;
                }
            }
            &_input {
                flex: 1;
                margin-left: 10px;
                height: 100%;
                border: 0;
            }
        }
        &_btn {
            padding: 15px;
            &_wrap {
                border: 0;
                border-radius: 5px;
                width: 100%;
                padding: 10px;
                font-size: 18px;
                font-weight: 500;
                letter-spacing: 1px;
                color: #fff;
                background: #ff4357;
            }
        }
        &_equities {
            &_wrap {
                .iconfont {
                    color: #ccc;
                }
            }
            &_dividend {
                .iconfont  {
                    font-size: 12px;
                    color: #ff4357;
                }
                .column_wrap {
                    height: 70px;
                }
                .column_left_txt {
                    line-height: 20px;
                }
            }
        }
    }   
}