.distribution {
    &_ranking {
        &_box {
            display: flex;
            flex-direction:column;
            height: 100%;
        }
        &_header {
            position: relative;
            display: flex;
            height: 150px;
            background: $color-background-orange;
            &_info {
                flex: 1;
                display: flex;
                align-items: center;
                flex-direction: column;
            }
            &_title {
                flex: 1;
                display: flex;
                align-items: flex-end;
                padding-bottom: 10px;
                font-size: 24px;
                color: $color-size-red;
            }
            &_txt {
                flex: 1;
                padding-top: 10px;
                display: flex;
                color: #fff;
            }
            &_brokerage {
                position: absolute;
                right: 20px;
                top: 50px;
            }
            &_num {
                text-align: center;
                color: $color-size-red;
            }
            &_numtxt {
                font-size: 16px;
                color: #fff;
            }
        }
        &_info {
            flex: 1;
            background: #ea4c25;
        }
        &_title {
            padding: 15px 0;
            font-size: 14px;
            text-align: center;
            color: #fff;
            background: $color-background-orangered;
        }
        &_table {
            margin: 10px;
            padding-top: 10px;
            background: #fff;
            &_wrap {
                padding-bottom: 10px;
                width: 100%;
                text-align: center;
            }
        }
        &_btn {
            padding: 10px;
            &_wrap {
                padding: 10px 0;
                border: 0;
                border-radius: 5px;
                width: 100%;
                font-size: 14px;
                text-align: center;
                color: #fff;
                background: $color-background-orangered;
            }
        }
    }
}