.goods {
    &_list {
        &_icon {
            float: right;
            @include box-size(25px,25px);
        }
        &_screen {
            position: relative;
            z-index: 3;
            border-bottom: 1px solid $color-line-gray;
            background: #fff;
            &_list {
                list-style: none;
                overflow: hidden;
                .active {
                    color: $color-size-red;
                    .txt{
                        color: $color-size-red;
                    }
                }
            }
            &_wrap {
                float: left;
                margin:15px -1px;
                font-size: 15px;
                border-left: 1px solid $color-line-gray;
                width: 25%;
                @include box-txts(5px,0,15px,15px,$color-size3);
            }
        }
        &_box {
            position: relative;
            overflow: hidden;
        }
        &_dialog {
            display: none;
            position: absolute;
            padding: 0 10px;
            width: 100%;
            left: 0;
            top:55px;
            z-index: 2;
            background: #fff;
            &_list {
                overflow: hidden;
                list-style: none;
                padding-bottom: 10px;
            }
            &_wrap {
                float: left;
                width: 33%;
                .active {
                    color: $color-size-red;
                    .txt {
                        color: $color-size-red;
                    }
                    .iconfont {
                        display: unset;
                    }
                }
                .iconfont {
                    display: none;
                }
            }
            &_item {
                @include box-txts(15px,0,15px,15px,$color-size-gray);
                padding: 0;
                padding-top: 10px;
            }
            &_classify {
                overflow: hidden;
            }
            &_title {
                border-top: 1px solid $color-line-gray;
                border-bottom: 1px solid $color-line-gray;
                @include box-txts(10px,0,15px,15px,$color-size-gray);
            }
            &_left {
                float: left;
                margin-top: 10px;
                height: 90px;
                overflow-y: scroll;
                width: 50%;
                .active {
                    background: $color-background-gray;
                }
            }
            &_right {
                float: left;
                margin-top: 10px;
                border-left: 1px solid $color-line-gray;
                overflow-y: scroll;
                box-sizing: border-box;
                width: 50%;
                height: 90px;
                .active {
                    background: $color-background-gray;
                }
            }
            &_option {
                @include box-txts(5px,0,15px,15px,#999);
            }
            &_btns {
                margin-top: 10px;
                border-top: 1px solid #efefef;
                @extend .clearfix;
            }
            &_btn {
                border: 0;
                @include box-txts(8px,0,25px,15px,#999);
                background: transparent;
            }
            .lbtn {
                float: left;
                margin-left: 10px;
                outline: none;
            }
            .rbtn {
                float: right;
                margin-right: 10px;
                outline: none;
                .txt {
                    color: $color-size-red;
                }
            }
            &_back {
                display: none;
                position: absolute;
                left: 0;
                top:0;
                width: 100%;
                height: 100%;
                background: rgba(0,0,0,.6);
                z-index: 1;
            }
        }
    }
}
.js-crosswise {
    .goods {
        &_list {
            &_wrap {
                float: left;
                margin-top: 5px;
                padding: 5px;
                box-sizing: border-box;
                width: 50%;
                display: block;
            }
            &_item {
                display: block;
                background: #fff;
            }
            &_thumbnail {
                position: relative;
                padding-top: 100%;
                width: 100%;
                background-position: center;
                background-size: cover;
                background-repeat: no-repeat;
            }
            &_commission {
                position: absolute;
                bottom: 0;
                left: 0;
                width: 100%;
                @include box-txts(2px,0,15px,12px,#fff);
                background: $color-background-orange;
            }
            &_info {                
                @include box-txts(5px,10px,20px,15px,$color-size3);
                text-align: left;
                .txt {
                    display: -webkit-box;
                    /* autoprefixer: off */
                    -webkit-box-orient: vertical;
                    /* autoprefixer: on */
                    -webkit-line-clamp:2;
                    overflow: hidden;
                    line-height: 20px;
                }
            }
            &_footer {
                padding: 10px;
                overflow: hidden;
            }
            &_price {
                float: left;
                @include box-txts(2px,0,18px,18px,$color-size-red)
            }
            &_btn {
                float: right;
                border: 0;
                @include box-txts(5px,10px,15px,15px,#fff);
                background: $color-size-red ;
            }
        }
    }
}

.js-lengthways {
    .goods {
        &_list {
            &_wrap {
                display: block;
                padding: 5px;
                background: #fff;
                border-bottom: 1px solid $color-line-gray;
            }
            &_item {
                overflow: hidden;
                display: flex;
            }
            &_thumbnail {
                float: left;
                position: relative;
                padding-top:110px;
                width:110px;
                background-position: center;
                background-size: cover;
                background-repeat: no-repeat;
            }
            &_commission {
                position: absolute;
                bottom: 0;
                left: 0;
                width: 100%;
                @include box-txts(2px,0,15px,12px,#fff);
                background: $color-background-orange;
            }
            &_msg {
                flex: 1;
			    -webkit-box-flex: 1;
			    overflow: auto;
                float: left;
                display: flex;
                flex-direction: column;
                justify-content: space-between;
            }
            &_info {
                margin-top: 10px;
                margin-left: 10px;
                text-align: left;
                @include rowhide(2,20px);
                font-size: 15px;
                color: $color-size3;
            }
            &_footer {
                padding: 0 10px;
                overflow: hidden;
            }
            &_price {
                float: left;
                @include box-txts(2px,0,18px,18px,$color-size-red)
            }
            &_btn {
                float: right;
                border: 0;
                @include box-txts(5px,10px,15px,15px,#fff);
                background: $color-size-red;
            }
        }
    }
}