*{
    margin: 0;
    padding: 0;
}
html,
body {
    height: 100%;
}
.webkit_box {
    display: flex;
    flex-direction: column;
    height: 100%;
}
.webkit_content {
    flex: 1;
    -webkit-box-flex: 1;
    overflow: auto;
}
  
.footer,
.header {
    overflow: hidden;
}
.box {
    overflow: hidden;
}