.distribution {
    &_qrcode {
        &_box {

        }
        &_header {
            height: 600px;
            @include background-url('../../img/qrcode.png');
        }
        &_info {
            padding-top:10px; 
        }
        &_title {
            .txt {
                color: $color-size6;
            }
        }
        &_titles {
            .column_wrap {
                padding: 10px 0;
                height: auto;
            }
            .column_title {
                align-items: flex-start;
            }
            .column_left_txt {
                .txt {
                    font-size: 14px;
                    color:$color-size2; 
                }
            }
            .column_center_txt {
                .txt {
                    font-size: 14px;
                    color:$color-size4; 
                }
            }
        }
        &_msg {
            margin: 15px;
            padding: 10px;
            background: #fe924a;
            line-height: 25px;
            color: #fff;
        }
    }
}