body {
    font-size: 12px;
    color: #333;
    font-family: Tahoma, "microsoft yahei", Geneva, sans-serif;
    line-height: 20px;
    background-color: #f3f3f3;
    -webkit-touch-callout: none;
    -webkit-font-smoothing: antialiased;
}

.float_lt {
    float: left;
}

.float_rt {
    float: right;
}

.clearfix:after {
    content: '';
    clear: both;
    display: block;
    height: 0;
    visibility: hidden;
}

.clearfix {
    zoom: 1;
}

.text_ellipsis {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.align_center {
    text-align: center;
}

.flex_center {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-flow: wrap;
}

.flex_around {
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-flow: wrap;
}

.flex_between {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-flow: wrap;
}

.flex_start {
    display: flex;
    align-items: center;
    flex-flow: wrap;
}

.flex_normal {
    display: flex;
    align-items: center;
}

.bg_cover {
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
}

.bg_contain {
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
}

.font_size_15 {
    font-size: 15px;
}

.font_size_13 {
    font-size: 13px;
}

.font_size_11 {
    font-size: 11px;
}

.display_block {
    display: block;
}

.margin_top_10 {
    margin-top: 10px;
}

.margin_right_4 {
    margin-right: 4px;
}

.margin_right_10 {
    margin-right: 10px;
}

.margin_left_10 {
    margin-left: 10px;
}

.margin_top_5 {
    margin-top: 5px;
}

.margin_btm_5 {
    margin-bottom: 5px;
}

.margin_btm_10 {
    margin-bottom: 10px;
}

.pad_left_percent_3 {
    padding-left: 3%;
}

.pad_top_percent_100 {
    padding-top: 100%;
}

.pad_0_10 {
    padding: 0 10px;
}

.pad_10 {
    padding: 10px;
}

