.van-sku-container {
	position: fixed;
	left: 0;
	bottom: 0;
	display: none;
	width: 100%;
	font-size: 14px;
	background: #fff;
	z-index: 2010;
}

.van-sku-body {
	max-height: 350px;
	overflow-y: scroll;
	-webkit-overflow-scrolling: touch;
}

.van-sku-body::-webkit-scrollbar {
	display: none;
}

.van-sku-group-container {
	margin-left: 15px;
	padding: 12px 0 2px;
	border-top: 1px solid #eaeaea;
}

.van-sku-header {
	margin-left: 15px;
}

.van-sku-header__img-wrap {
	position: relative;
	float: left;
	// margin-top: -10px;
	width: 80px;
	height: 80px;
	background: #f8f8f8;
	border-radius: 2px;
}

.van-sku-header__img-wrap img {
	position: absolute;
	margin: auto;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	max-width: 100%;
	max-height: 100%;
}

.van-sku-header__goods-info {
	padding: 10px 60px 10px 10px;
	min-height: 82px;
	overflow: hidden;
	-webkit-box-sizing: border-box;
	box-sizing: border-box;
}

.van-sku__goods-name {
	font-size: 12px;
}

.van-sku__price-symbol {
	vertical-align: middle;
}

.van-sku__price-num {
	font-size: 16px;
	vertical-align: middle;
}

.van-sku__goods-price {
	color: #f44;
	margin-top: 10px;
	vertical-align: middle;
}

.van-sku__close-icon {
	position: absolute;
	top: 10px;
	right: 15px;
	width: 20px;
	height: 20px;
	background: url(../img/icon-put-down.png) no-repeat center;
	background-size: contain;
}

.van-sku-row {
	margin: 0 15px 10px 0;
}

.van-sku-row:last-child {
	margin-bottom: 0;
}

.van-sku-row__title {
	padding-bottom: 10px;
}

.van-sku-row__item {
	display: inline-block;
	padding: 5px 9px;
	margin: 0 10px 10px 0;
	height: 28px;
	min-width: 52px;
	line-height: 16px;
	font-size: 12px;
	color: #333;
	text-align: center;
	border: 1px solid #999;
	border-radius: 3px;
	-webkit-box-sizing: border-box;
	box-sizing: border-box;
}

.van-sku-row__item--active {
	color: #fff;
	border-color: #f44;
	background: #f44;
}

.van-sku-row__item--disabled {
	background: #e8e8e8;
	border-color: #e5e5e5;
	color: #c9c9c9;
}

.van-sku-stepper-stock {
	padding: 12px 0;
	margin-left: 15px;
	border-top: 1px solid #eaeaea;
}

.van-sku-stepper-container {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align: center;
	-ms-flex-align: center;
	align-items: center;
	height: 30px;
	margin-right: 20px;
}

.van-sku__stepper {
	float: right;
}

.van-sku__stepper-title {
	float: left;
	line-height: 30px;
}

.van-sku__stock {
	display: inline-block;
	margin-right: 10px;
	color: #999;
	font-size: 12px;
}

.van-sku__quota {
	display: inline-block;
	color: #f44;
	font-size: 12px;
}

.van-sku-messages {
	padding-bottom: 10px;
	background: #f8f8f8;
}

.van-sku-messages__image-cell .van-cell__title {
	max-width: 90px;
}

.van-sku-messages__image-cell .van-cell__value {
	text-align: left;
}

.van-sku-img-uploader {
	display: inline-block;
}

.van-sku-img-uploader__header {
	padding: 0 10px;
	border: 1px solid #e5e5e5;
	line-height: 24px;
	border-radius: 3px;
	font-size: 12px;
}

.van-sku-img-uploader__header .van-icon {
	top: 3px;
	margin-right: 5px;
	font-size: 14px;
}

.van-sku-img-uploader__img {
	height: 60px;
	width: 60px;
	float: left;
	margin: 10px 10px 0 0;
	position: relative;
	border: #e5e5e5 1px solid;
}

.van-sku-img-uploader__img img {
	max-width: 100%;
	max-height: 100%;
	top: 50%;
	position: relative;
	-webkit-transform: translateY(-50%);
	transform: translateY(-50%);
}

.van-sku-img-uploader__delete {
	position: absolute;
	color: #f44;
	top: -12px;
	right: -14px;
	z-index: 1;
	padding: 6px;
}

.van-sku-img-uploader__delete::before {
	border-radius: 14px;
	background-color: #fff;
}

.van-sku-img-uploader__uploading {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	margin: auto;
	width: 20px;
	height: 20px;
}

.van-sku-actions {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
}

.van-button {
	position: relative;
	padding: 0;
	display: inline-block;
	height: 45px;
	line-height: 43px;
	border-radius: 3px;
	-webkit-box-sizing: border-box;
	box-sizing: border-box;
	font-size: 16px;
	text-align: center;
	-webkit-appearance: none;
}

.van-button::before {
	content: " ";
	position: absolute;
	top: 50%;
	left: 50%;
	opacity: 0;
	width: 100%;
	height: 100%;
	border: inherit;
	border-color: #000;
	background-color: #000;
	border-radius: inherit;
	-webkit-transform: translate(-50%, -50%);
	transform: translate(-50%, -50%);
}

.van-button:active::before {
	opacity: .3;
}

.van-button--unclickable:before {
	display: none;
}

.van-button--default {
	color: #333;
	background-color: #fff;
	border: 1px solid #eee;
}

.van-button--primary {
	color: #fff;
	background-color: #4b0;
	border: 1px solid #4b0;
}

.van-button--danger {
	color: #fff;
	background-color: #f44;
	border: 1px solid #f44;
}

.van-button--large {
	width: 100%;
	height: 50px;
	line-height: 48px;
}

.van-button--normal {
	padding: 0 15px;
	font-size: 14px;
}

.van-button--small {
	height: 30px;
	padding: 0 8px;
	min-width: 60px;
	font-size: 12px;
	line-height: 28px;
}

.van-button--loading .van-loading {
	display: inline-block;
}

.van-button--loading .van-button__text {
	display: none;
}

.van-button--mini {
	display: inline-block;
	width: 50px;
	height: 22px;
	line-height: 20px;
	font-size: 10px;
}

.van-button--mini+.van-button--mini {
	margin-left: 5px;
}

.van-button--block {
	width: 100%;
	display: block;
}

.van-button--bottom-action {
	width: 100%;
	height: 50px;
	line-height: 50px;
	border: 0;
	border-radius: 0;
	font-size: 16px;
	color: #fff;
	background-color: #f85;
}

.van-button--bottom-action.van-button--primary {
	background-color: #f44;
}

.van-button--disabled {
	color: #999;
	background-color: #eee;
	border: 1px solid #e5e5e5;
}