.goods {
    &_classify {
        &_box {
            position: absolute;
            left: 0;
            top: 0;
            bottom: 0;
            right: 0;
            width: 100%;
            height: 100%;
            background: #f5f5f5;
        }
        &_wrap {
            display: flex;
            height: 100%;
            overflow: hidden;
        }
        &_sidebar {
            // float: left;
            overflow-y: auto;
            width: 22%;
            &_option {
                display: flex;
                align-items: center;
                justify-content: center;
                height: 46px;
                color: #333;
                padding: 0;
                line-height: 15px;
                font-size: 15px;
                text-align: center;
                border-left: 3px solid #eee;
                border-bottom: 1px solid #eaeaea;
            }            
            .active {
                box-sizing: border-box;
                background: #fff;
                border-left: 3px solid #ff2c29;
            }
        }
        &_list {
            @extend .text_ellipsis;
            padding: 10px;
            &_kind {
                display: flex;
                flex-flow: wrap;
            }
            &_wrap {
                width: 30%;
                margin: 1%;
                padding:5px;
                box-shadow: 0 1px 14px #efefef;
            }
            &_container {
                flex: 1;
                overflow: auto;
                background-color: $white-color;
            }
            &_box {
                display: none;
                opacity: 0;
            }
            &_box.active {
                display: block;
                opacity: 1;
            }
        }
        &_icon {
            overflow: hidden;
            @include box-size(80px,80px);
            background-position: center;
            background-repeat: no-repeat;
            background-size: cover;
            border-radius: 50%;
            margin: 0 auto;
        }
        &_title {
            margin-top: 10px;
            margin-bottom: 5px;
            @include box-txts(0,0,15px,15px,#222);
            .txt {
                display: block;
                @extend .text_ellipsis;
            }
        }
        &_logo {
            // margin: 5px;
            height: 112px;
            margin-bottom: 12px;
            background: #cfcfcf;
            background-position: center;
            background-repeat: no-repeat;
            background-size: cover;
        }
    }
}